body[scheme="bright_light"] {
    --title_icon: #fff;
    --state_time: rgb(126, 126, 126);
    --profile_error: rgb(119, 118, 118);
    --link: #3f8ae0;
    --state_description: rgb(99, 99, 99);
    --button_home: rgb(140, 128, 245);
}
body[scheme="space_gray"] {
    --state_time: rgb(168, 168, 168);
    --link: rgb(91, 119, 245);
    --state_description: rgb(179, 177, 177);
}
* {
    margin: 0;
}


a {
    color: var(--accent);
    text-decoration: none;
}

#stickers {
    overflow-x: hidden;
}

.news-block {
    width: 92%;
    border-radius: 5px;
    height: auto;
    margin: auto;
    margin-top: 10px;
    position: relative;
}

.news-state_title {
    margin-left: 1px;
    padding: 0px 0px ;
    font-weight: 700;
    font-size: 18px;
    min-height: 20px;
}

.news-state_image {
    margin-top: 5px;
    height: auto;
    position: relative;
    width: 100%;
    border-radius: 5px;
}

.news-state_image img {
    position: relative;
    height: auto;
    width: 100%;
    border-radius: 7px;
}

.news-state_description {
    padding-top: 7px;
    padding-left: 0px;
    font-weight: 600;
    font-size: 12.5px;
    color: var(--state_description)
}

.news-state_title_icon {
    position: absolute;
    right: 2.5px;
    top: 4px;
    color: var(--title_icon);
}

#anim:active{
    -moz-user-select: none;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    opacity: 0.7;
}

.state-title {
    display: flex;
    font-size: 18px;
    font-weight: 700;
}

.state-time {
    margin-top: -2px;
    color: var(--state_time);
    margin-left: 14px;
    font-size: 13px;
}

.state-text {
    margin-top: 10px;
    white-space: pre-wrap;
    padding: 5px 10px 5px 15px;
}

.state-image {
    margin-top: 10px;
    width: 100%;
    height: auto;
}

.state-image img {
    width: 100%;
    height: auto;
}

.profile-error {
    position: absolute;
    text-align: center;
    padding: 0px 12.5px 0px 12.5px;
    top: calc(50% - 25px);
    height: 50px;
    color: var(--profile_error);
}

.state-title_icon {
    position: relative;
    top: 10.5px;
    color: rgb(92, 92, 92);
}
