.sticker_input {
    margin: auto;
    width: 90%;
    position: relative;
    margin-top: 20px;
}

.sticker_left {
    float: left;
    margin: 15px 12.5px 0px 18px;
    width: 100px;
    height: 100px;
}

.sticker_right {
    padding-top: 22.5px;
}

.sticker_name {
    margin-left: 18px;
    margin-top: 15px;
    padding-top: 10px;
    font-weight: 600;
}

.sticker_author {
    margin-left: 18px;
    margin-top: 4px;
}

.sticker_decription {
    margin: 35px 10px 10px 18px;
}

.sticker_image img {
    border-radius: 25px;
    display: block;
    position: relative;
    margin: auto;
    height: auto;
    width: 90%;
}

.sticker_scroll {
    margin-top: 20px;
    padding-left: 18px;
    width: 100%;
    overflow-x: scroll;
    white-space: nowrap;
}
